import { useAuth } from 'hooks/useAuth';
import { Link } from "./Navigation.styled.jsx";

export const Navigation = () => {
const { isLoggedIn } = useAuth();
  
  return (
    <nav>
      <Link  to="/">
        Home
      </Link>
      {isLoggedIn && (
        <Link to="/contacts">
          Contacts
        </Link>
      )}
    </nav>
  );
};